import React, { useEffect, useState } from "react";
import {
  Drawer,
  List,
  Divider,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
  Avatar,
  Button,
  Hidden,
} from "@mui/material";

import PersonSearchIcon from "@mui/icons-material/PersonSearch";
import WorkIcon from "@mui/icons-material/Work";
import CodeIcon from "@mui/icons-material/Code";
import StarsIcon from "@mui/icons-material/Stars";
import SchoolIcon from "@mui/icons-material/School";
import PublicIcon from "@mui/icons-material/Public";
import DownloadForOfflineIcon from "@mui/icons-material/DownloadForOffline";
import MenuIcon from "@mui/icons-material/Menu";
import ClearIcon from "@mui/icons-material/Clear";

import { Link } from "react-router-dom";

import { deepOrange } from "@mui/material/colors";

import AOS from "aos";
import "aos/dist/aos.css";

const drawerWidth = "20%";

function SidebarComponent() {
  const [hideBarMobile, setHideBarMobile] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  useEffect(() => {
    AOS.init({
      offset: 500, // Offset (in pixels) from the top of the window to st/adminart animations
      duration: 1500, // Duration (in milliseconds) of the animation
      easing: "ease-in-out", // Easing function for the animation
    });
  }, []);

  function handleMobile() {
    if (hideBarMobile == true) {
      setHideBarMobile(false);
      setIsOpen(false);
    } else {
      setHideBarMobile(true);
      setIsOpen(true);
    }
  }

  return (
    <div>
      <Hidden smUp>
        <Button
          sx={{
            position: "absolute",
            right: 0,
            margin: "10px",
            cursor: "pointer",
          }}
          onClick={handleMobile}
          color="light">
          <MenuIcon fontSize="large" />
        </Button>
        <Drawer
          variant="temporary"
          anchor="right"
          open={isOpen}
          onClose={handleMobile}
          sx={{
            width: 200,
            flexShrink: 0,
            "& .MuiDrawer-paper": {
              width: 200,
              boxSizing: "border-box",
            },
          }}>
          <Button
            sx={{
              position: "absolute",
              right: 0,
              margin: "10px",
              cursor: "pointer",
            }}
            onClick={handleMobile}
            color="danger">
            <ClearIcon fontSize="large" />
          </Button>
          <List
            sx={{
              marginTop: "50px",
            }}>
            <Link to={"/admin/dashboard"}>
              <ListItem disablePadding>
                <ListItemButton>
                  <ListItemIcon>
                    <PersonSearchIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Introduce"} />
                </ListItemButton>
              </ListItem>
            </Link>
            <Link to={"/admin/experiences"}>
              <ListItem disablePadding>
                <ListItemButton>
                  <ListItemIcon>
                    <WorkIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Experiences"} />
                </ListItemButton>
              </ListItem>
            </Link>
            <Link to={"/admin/projects"}>
              <ListItem disablePadding>
                <ListItemButton>
                  <ListItemIcon>
                    <CodeIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Projects"} />
                </ListItemButton>
              </ListItem>
            </Link>
          </List>
          <Divider />
          <List>
            <Link to={"/admin/educations"}>
              <ListItem disablePadding>
                <ListItemButton>
                  <ListItemIcon>
                    <SchoolIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Educations"} />
                </ListItemButton>
              </ListItem>
            </Link>
            <Link to={"/admin/certificates"}>
              <ListItem disablePadding>
                <ListItemButton>
                  <ListItemIcon>
                    <StarsIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Certificates"} />
                </ListItemButton>
              </ListItem>
            </Link>
            <Link to={"/admin/media-social"}>
              <ListItem disablePadding>
                <ListItemButton>
                  <ListItemIcon>
                    <PublicIcon />
                  </ListItemIcon>
                  <ListItemText primary={"Media Social"} />
                </ListItemButton>
              </ListItem>
            </Link>
          </List>
        </Drawer>
      </Hidden>
      <Hidden smDown>
        <Drawer
          sx={{
            width: drawerWidth,
            flexShrink: 0,
            "& .MuiDrawer-paper": {
              width: drawerWidth,
              boxSizing: "border-box",
            },
          }}
          variant="permanent"
          anchor="left">
          <div data-aos="fade-right">
            <Toolbar
              sx={{
                display: "flex",
                justifyContent: "center",
                margin: 2,
              }}>
              <Avatar
                sx={{
                  bgcolor: deepOrange[500],
                  height: 80,
                  width: 80,
                }}
                alt="Remy Sharp"
                src="data:image/jpeg;base64,"
              />
            </Toolbar>
            <div className="block text-center mb-5 leading-none capitalize">
              <h3 className="font-medium mb-0">Muhammad Arifin Habibi</h3>
              <p className="mt-0 font-thin text-sm">website developer</p>
              <Button
                variant="outlined"
                sx={{
                  textTransform: "capitalize",
                  marginTop: "10px",
                }}
                color="secondary">
                <DownloadForOfflineIcon
                  sx={{
                    marginRight: "2px",
                  }}
                />{" "}
                Resume
              </Button>
            </div>
            <Divider />
            <List>
              <Link to={"/admin/dashboard"}>
                <ListItem disablePadding>
                  <ListItemButton>
                    <ListItemIcon>
                      <PersonSearchIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Introduce"} />
                  </ListItemButton>
                </ListItem>
              </Link>
              <Link to={"/admin/experiences"}>
                <ListItem disablePadding>
                  <ListItemButton>
                    <ListItemIcon>
                      <WorkIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Experiences"} />
                  </ListItemButton>
                </ListItem>
              </Link>
              <Link to={"/admin/projects"}>
                <ListItem disablePadding>
                  <ListItemButton>
                    <ListItemIcon>
                      <CodeIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Projects"} />
                  </ListItemButton>
                </ListItem>
              </Link>
            </List>
            <Divider />
            <List>
              <Link to={"/admin/educations"}>
                <ListItem disablePadding>
                  <ListItemButton>
                    <ListItemIcon>
                      <SchoolIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Educations"} />
                  </ListItemButton>
                </ListItem>
              </Link>
              <Link to={"/admin/certificates"}>
                <ListItem disablePadding>
                  <ListItemButton>
                    <ListItemIcon>
                      <StarsIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Certificates"} />
                  </ListItemButton>
                </ListItem>
              </Link>
              <Link to={"/admin/media-social"}>
                <ListItem disablePadding>
                  <ListItemButton>
                    <ListItemIcon>
                      <PublicIcon />
                    </ListItemIcon>
                    <ListItemText primary={"Media Social"} />
                  </ListItemButton>
                </ListItem>
              </Link>
            </List>
          </div>
        </Drawer>
      </Hidden>
    </div>
  );
}

export default SidebarComponent;
